import React, { Component } from 'react'
import { Link } from "react-scroll";
import logo from '../components/logo.png';

class Navbar extends Component {
    state = {};

    render() {
        return (
            <header>
                <div className="container">
                    <div className="header-items">
                        <h1 className="logo">
                            <img className="logo" src={logo} alt="Giorgi Arsenadze"></img>
                        </h1>
                        <nav className="navbar">
                            <ul className="menu">
                                <li className="button noselect">
                                    <Link
                                        to="about"
                                        spy={true}
                                        smooth={true}
                                        offset={-74}
                                        duration={500}>
                                        ABOUT
                                    </Link>
                                </li>
                                <li className="button noselect">
                                    <Link
                                        to="research"
                                        spy={true}
                                        smooth={true}
                                        offset={-74}
                                        duration={500}>
                                        RESEARCH
                                    </Link>
                                </li>
                                <li className="button noselect">
                                    <Link
                                        to="contact"
                                        spy={true}
                                        smooth={true}
                                        offset={-74}
                                        duration={500}>
                                        CONTACT
                                    </Link>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </div>
            </header>
        );
    }

}

export default Navbar;