import React, { Component} from "react";

class Footer extends Component{
    state={};
    render() {
        return <div className="footer-section" id="footer">
            <section class="copyrigt-section">
                <div class="container">
                    <div class="copyrigt-text">
                        Copyright © {(new Date().getFullYear())} Giorgi Arsenadze - All Rights Reserved
                    </div>
                </div>
            </section> 
        </div>;
    }
}

export default Footer;