import React, { Component } from "react";
import arsmen from '../../arsmen.jpg';
class About extends Component {
    state = {};
    render() {
        return <div className="about-section" id="about">

            <div className='profile-section' >
                <dic className="container">
                    <div className='profile' >
                        <div className="profile-image">
                            <div className="image-cropper">
                                <img className="image" src={arsmen} alt="Giorgi Arsenadze"></img>
                            </div>
                            <div className="name">Giorgi Arsenadze</div>
                            <div className="title"> Physics Student</div>
                        </div>
                        <div className="cv-block">
                            <h2>Education </h2>
                            <p>
                                <div className="item">
                                    <div className="a"><div>2019 - Present</div>
                                        <div className="b"><b>Graduate Student in physics</b> <br></br> </div></div>
                                    <div className="d">New York University </div>
                                </div>
                                <br></br>
                                <div className="item">
                                    <div className="a"><div className="c">2015 - 2019</div>
                                        <div className="b"><b>B.S, major in physics</b> <br></br> GPA: 3.76</div></div>
                                    <div className="d">Free university of Tbilisi</div>
                                </div>
                                <br></br>
                                <div className="item">
                                    <div className="a"><div className="c">2009 - 2015</div>
                                        <div className="b"><b>Secondary school</b> <br></br> GPA: 4.00</div></div>
                                    <div className="d">N199 mathemathics and physics public school named after Komarovi</div>
                                </div>
                            </p>
                            <br></br>
                            <h2>Languages </h2>
                            <p>
                                Georgian (mother
                                tongue)  <br></br>
                                English (fluent level)  <br></br>
                                Russian
                                (conversational level)
                            </p>
                            <br></br>
                            <h2>Programming/Computer Skills</h2>
                            <p>
                                Java (familiar) <br></br>
                            C++ (familiar)<br></br>
                            Python (familiar)<br></br>
                            MATLAB (proficient)<br></br>
                            Wolfram Mathematica (proficient)<br></br>
                            Latex (proficient)<br></br>
                            </p>

                        </div>
                    </div>
                </dic>
            </div>

        </div>;

    }
}

export default About;